
.h2{
  margin-top: 30px;
  font-size: 18px;
}
.login{
  margin: 0px auto;
  text-align: center;
}
form{
  margin: 60px 0px;
}
#id, #pw, input[type='button']{
  font-size: 12px;
  width: 320px;
  height: 50px;
  margin: 10px;
  padding: 10px;
  border-radius: 5px;
  box-sizing: border-box;
  border:1px solid #ccc;
  font-weight: bold;
}
input[type='button']{
  background-color: rgb(95, 0, 128);
  border: none;
  color: #fff;
}
.join{
  background-color: #fff !important;
  color: rgb(95, 0, 128) !important;
  border: 1.5px solid rgb(95, 0, 128) !important;
}