@charset "utf-8";

ol, ul, li{
  list-style: none;
}
h3{
  text-align: center;
  font-size: 26px;
  color: #333;
}
/* product list 서식 */
.product_list{
  width: 1050px;
  height: 434px;
  display: flex;
  justify-content:space-between;
  margin:0px auto;
  padding: 0px;
}
.product_list div{
  position: absolute; 
  right: 10px;
  bottom: 130px;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  background-color: rgb(147, 117, 175);
  border-radius:50%;
  font-size: 15px;
  color: #fff;
  cursor: pointer;
}
.product_list > li >p{
  font-size: 16px;
}
.product_list > li{
  position: relative;
  color: #333;
  width: 249px;
  height: 434px;
  overflow: hidden;
}
.product_list > li img{
  height: 320px;
  transition: 0.2s;
}
.product_list > li img:hover{
  transform: scaleX(1.1);
}
.red{
  color: red;
  font-weight: bold;
  padding-right: 10px;
}