@charset "utf-8";


*{margin: 0px; padding: 0px;}
ul,li,ol{list-style: none;}
body{
  font-family: "맑은 고딕",Arial,sans-serif;
  font-size: 12px;
  color: #333;
}
a{text-decoration: none; color: #333;}

header{
  width: 100%;
  height: 156px;
  box-shadow: 0px 0px 3px #333;
}
.header_inner{
  width: 1050px;
  height: 100%;
  margin: 0px auto;
  position: relative;
}
.inner_top >.logo_wrap{
  position: absolute;
  top: 44%;
  transform: translateY(-50%);
  padding: 0px;
  width: 100%;
  display: flex;
  justify-content: left;
  align-items: center;
}
.inner_top >.logo_wrap >li{
  font-size: 18px;
  margin-left: 16px;
  color: #ccc;
}
.inner_top >.logo_wrap >li:nth-child(1){
  margin-top: 5px;
}
.inner_top >.logo_wrap >li:nth-child(2){
  color: #5f0080;
  font-weight: bold;
}
.lnb{
  position: absolute;
  right: 0px;
  top: 10px;
}
.lnb span{
  font-size: 11px;
  padding-left: 10px;
  font-weight: bold;
  color: #555;
}
.lnb span:first-child{
  color: #5f0080;
}
.lnb span:first-child::before{
  display: none;
}
.lnb span::before{
  content: "|";
  padding-right: 10px ;
}
.search{
  position: absolute;
  left: 31%;
  top: 45%;
  transform: translateY(-50%);
  z-index: 10;
}
#search{
  width: 400px;
  height: 48px;
  font-size: 14px;
  padding: 15px;
  box-sizing: border-box;
  border-radius: 5px;
  border:1.5px solid #5f0080;
}
.search label {
  position: absolute;
  right: 0px;
}
.search label img{
  width: 50px;
}
.icon_wrap{
  position: absolute;
  right: 0px;
  top: 45%;
  transform: translateY(-50%);
  display: flex;
  justify-content: right;
  align-items: center;
}
.icon_wrap li{
  padding-left: 20px;
}
.inner_bottom{
  width: 100%;
  position: absolute;
  bottom: 5px;
}
.inner_bottom ul{
  width: 100%;
  position: absolute;
  bottom: 13px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  justify-content: center;
}
.inner_bottom ul li{
  padding: 0px 45px;
  font-size: 16px;
  font-weight: bold;
}
.inner_bottom div:first-child{
  position: absolute;
  bottom: 15px;
  left: 0px;
  font-size: 16px;
  font-weight: bold;
}
.inner_bottom div:first-child img{
  margin-right: 10px;
}
.inner_bottom div:last-child{
  position: absolute;
  bottom: 8px;
  right: 0px;
  border: 1px solid #ccc;
  border-radius: 16px;
  width: 130px;
  height: 32px;
  line-height: 32px;
  text-align: center;
}
.inner_bottom div:last-child span{
  color: #5f0080;
  font-weight: bold;
}
.banner{
  width: 100%;
  height: 140px;
  padding: 40px 0px;
}
.banner img{
  width: 100%;
  height: 100%;
}
main, .product, .notfound{
  width: 1050px;
  padding: 30px 0px;
  margin: auto;
}
.main_slide{
  margin: auto;
  width: 1900px;
  height: 370px;
  position: relative;
  overflow: hidden;
}
.btn_box div{
  background-color: rgba(255, 255, 255, 0);
  border: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: 0.2s;
}
.main_slide:hover .prev,.main_slide:hover .next{
  opacity: 1;
}
.prev{
  opacity: 0;
  left: 15%;
}
.prev img{
  transform: rotate(180deg);
}
.next{
  opacity: 0;
  right: 15%;
}
.main_slide ul{
  position: absolute;
  top: 0px;
  left: 0px;
  display: flex;
  width: 9500px;
  height: 370px;
  transition: 0.3s;
}
.main_slide ul li{
  width: 1900px;
  height: 370px;
}
.main_slide img{
  width: 100%;
}
.product h2{
  text-align: center;
  font-size: 28px;
  padding-bottom: 10px;
}
.notfound{
  width: 100%;
  height: 700px;
}
.notfound p{
  font-size: 25px;
  text-align: center;
  font-weight: bolder;
}
.notfound p span{
  padding-top: 30px;
  display: block;
  font-size: 16px;
  color: #ccc;
  font-weight: 100;
}
.notfound p input{
  width: 150px;
  height: 44px;
  background-color: #5f0080;
  border-radius: 5px;
  color: #fff;
  font-weight: bold;
  border: none;
}
footer{
  width: 100%;
  height: 580px;
  padding-top: 30px;
  border-top: 1px solid #ccc;
}
.f_inner{
  width: 1050px;
  margin: 0px auto;
  height: 470px;
  position: relative;
}
.address_wrap{
  width: 100%;
  height: 110px;
  background-color: rgb(236, 236, 236);
  text-align: center;
  position: relative;
}
.address_wrap address{
  font-style: normal;
  text-transform: uppercase;
  font-size: 7px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}
.txt_box1{
  position: absolute;
  top: 0px;
  left: 0px;
}
.txt_box1 h2{
  font-size: 20px;
}
.txt_box1 div{
  font-size: 28px;
  font-weight: bold;
}
.txt_box1 div span{
  font-size: 15px;
  padding-left: 10px;
}
.txt_box2{
  font-weight: bold;
  width: 456px;
  position: absolute;
  top: 0px;
  right: 0px;
}
.txt_box2 ul{
  padding: 0px;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  padding-bottom: 30px;
}
.txt_box2 div{
  color: rgb(138, 138, 138);
  line-height: 20px;
}
.color{
  color: #5f0080;
}
.top{
  display: inline-block;
  position: fixed;
  right: 40px;
  bottom: 30px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  line-height: 50px;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  z-index: 1000;
  color: #ccc;
}